import { useRegistry } from '@wordpress/data';
import { useEffect } from '@wordpress/element';

export const withProxiedDispatch = ( WrappedComponent, from, to ) => ( props ) => {
	const registry = useRegistry();

	useEffect( () => {
		registry.addDispatchProxy( from, to );

		return () => {
			registry.removeDispatchProxy( from );
		};
	}, [] );

	return <WrappedComponent { ...props } />;
};

export const withProxiedSelect = ( WrappedComponent, from, to ) => ( props ) => {
	const registry = useRegistry();

	useEffect( () => {
		registry.addSelectProxy( from, to );

		return () => {
			registry.removeSelectProxy( from );
		};
	}, [] );

	return <WrappedComponent { ...props } />;
};
